import {Stores} from './stores'

export class Recommendations {

    static ProcessRecommendations(data: any): string[] {
        let parent = data.recommendations
        let set = new Set()

        for(let i=0; i<parent.length;++i) {
            let recommendation = parent[i]
            let rules = recommendation.rules
            
            let passedCount = 0
            for(let j=0; j<rules.length; ++j) {
                let rule = rules[j]
                if(!Recommendations.ProcessRule(rule)) {
                    break
                } else {
                    passedCount++
                }
            }
    
            if(passedCount === rules.length){
                // recommend this.
                set.add(recommendation.message)
            }
        }
        return Array.from(set) as string[]
    }

    static ProcessRule(rule: any): boolean {
        let section = rule.section
        let prop = rule.prop
        let op = rule.op
        let value = rule.value
    
        let data = Recommendations.GetDataObject(section)
        let props = prop.split('.')
        let propertyValue = Recommendations.GetPropertyValue(data, props)
    
        return Recommendations.YieldResult(propertyValue, op, value)
    }
    

    static GetDataObject(section: any): any {
        let result = null
        switch(section) {
            case "about": 
                        result = Stores.about
                        break
            case "income":
                        result = Stores.income
                        break
            case "deduction":
                        result = Stores.deduction
                        break
            case "taxcredit":
                        result = Stores.taxcredit
                        break
            case "tax": result = Stores.tax
                        break
            default:    break
        }
        return result
    }
    
    static GetPropertyValue(data: any, props: any[]): number {
        let holder = data
        for(let i=0; i<props.length; i++) {
            holder = holder[props[i]]
        }
        return holder.value
    }
    
    static YieldResult(propertyValue: number, op: string, value: number): boolean {
        let result = false
        switch(op) {
            case "=": result = propertyValue === value 
                      break
            case ">": result = propertyValue > value 
                      break
            case "<": result = propertyValue < value 
                      break
            case "!=": result = propertyValue !== value 
                      break
            default: break
        }
        return result
    }
}
