import { h } from 'preact';
import { route } from 'preact-router';
import { Stores } from '../../stores/stores';
import Total from '../../components/Total';
import { Chart } from "react-google-charts";
import { useEffect, useState } from 'preact/hooks';
import { Recommendations } from '../../stores/recommendations';

const Analysis = () => {

    const options = {};
    const [showAlertData, setShowAlertData] = useState(false);
    const [flowData, setFlowData] = useState([]);
    const [gaugeData, setGaugeData] = useState([]);
	const [incomeChartData, setIncomeChartData] = useState([]);
    const [deductionData, setDeductionData] = useState([]);
	const [realDeductionData, setRealDeductionData] = useState([]);
    const [recommendationsData, setRecommendationsData] = useState([] as string[]);

	const deduction_options = {
		title: "Income Distribution",
		pieHole: 0.5,
		is3D: false,
		titleTextStyle: {
			fontName: 'Helvetica',
			fontSize: 14,
		}
	};

	const real_deduction_options = {
		title: "Deductions/Pre-Tax/Taxable",
		pieHole: 0.5,
		is3D: false,
		titleTextStyle: {
			fontName: 'Helvetica',
			fontSize: 14,
		}
	};

    const income_options = {
		title: "Gross Income",
		pieHole: 0.5,
		is3D: false,
		titleTextStyle: {
			fontName: 'Helvetica',
			fontSize: 14,
		}
	};

	const showSuccessAlert = () => {
		if(showAlertData) {
			return <div id="bs-alert" class="alert alert-success alert-dismissible fade show" role="alert">
						Your estimated income model is saved.
					<button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
			  	</div>
		}
	}

	useEffect(() => {
		let temp: any = []
		temp.push(['income', 'amount'])
		
		let left_over = parseInt(Stores.income.agi.value) - parseInt(Stores.deduction.deduction.value[1])
		let deduction = 0
		if(left_over >= 0) {
			deduction = parseInt(Stores.deduction.deduction.value[1])
		} else {
			deduction = parseInt(Stores.deduction.deduction.value[1]) + left_over
			if(deduction < 0) {
				deduction = 0
			}
		} 
		temp.push(['Pretax income', parseInt(Stores.income.pretax.value)])
		temp.push(['Deductions', deduction])
		temp.push(['Taxable income', parseInt(Stores.income.agi.value) - deduction])

		setRealDeductionData(temp)

	}, [Stores.deduction.deduction.value, Stores.income.agi.value]);

	useEffect(() => {
		fetch(`/data/recommendations.json`, {
			method:   'GET',
			headers:  {
			'Content-Type': 'application/json',
			}
			})
			.then(response => response.json())
			.then(data => {
				setRecommendationsData(Recommendations.ProcessRecommendations(data))
			})
		
	}, [Stores.deduction.deduction.value, Stores.income.agi.value, Stores.taxcredit.total_tax_credit.value]);
	

    useEffect(() => {
		let temp: any = []
		temp.push(['income', 'amount'])
		
		let left_over = parseInt(Stores.income.agi.value) - parseInt(Stores.deduction.deduction.value[1])
		let deduction = 0
		if(left_over >= 0) {
			deduction = parseInt(Stores.deduction.deduction.value[1])
		} else {
			deduction = parseInt(Stores.deduction.deduction.value[1]) + left_over
			if(deduction < 0) {
				deduction = 0
			}
		} 
		temp.push(['Pretax income', parseInt(Stores.income.pretax.value)])
		temp.push(['Deductions', deduction])
        temp.push(['Tax Credits', Stores.taxcredit.total_tax_credit.value])
		temp.push(['Tax Due', Math.max(0, Stores.tax.due.value)])
        temp.push(['Net Income', Stores.tax.taxable_income.value - Stores.tax.tax_amount.value - Stores.income.ssa_medicare_contrib.value])
		temp.push(['SSA/Medicare', Stores.income.ssa_medicare_contrib.value])
		setDeductionData(temp)

	}, [Stores.deduction.deduction.value, Stores.income.agi.value]);


	useEffect(() => {
		let temp : any = [] 
		temp.push(['income', 'amount'])
		
		if(Stores.income.w2.value > 0) {
			temp.push(['W2', parseInt(Stores.income.w2.value)])
		}
		if(Stores.income.f1099misc.amt.value > 0) {
			temp.push(['Misc/Nec/Rental', parseInt(Stores.income.f1099misc.amt.value)])
		}
		if(Stores.income.rsu.amt.value > 0) {
			temp.push(['RSU/Options', parseInt(Stores.income.rsu.amt.value)])
		}
		if(Stores.income.f1099b_longterm.amt.value > 0 || Stores.income.f1099b_shortterm.amt.value > 0) {
			temp.push(['Capital gain', parseInt(Stores.income.f1099b_longterm.amt.value) + parseInt(Stores.income.f1099b_shortterm.amt.value)])
		}
		if(Stores.income.f1099r.amt.value > 0) {
			temp.push(['Retirement dist.', parseInt(Stores.income.f1099r.amt.value)])
		}
		if(Stores.income.f1099g.amt.value > 0) {
			temp.push(['Unemployment benefits', parseInt(Stores.income.f1099g.amt.value)])
		}
		if(Stores.income.farm.amt.value > 0) {
			temp.push(['Farm income', parseInt(Stores.income.farm.amt.value)])
		}
		if(Stores.income.f1099_div_qualified.amt.value > 0 || Stores.income.f1099_div.amt.value > 0) {
			temp.push(['Dividend', parseInt(Stores.income.f1099_div_qualified.amt.value) + parseInt(Stores.income.f1099_div.amt.value)])
		}
		if(Stores.income.f1099_int.amt.value > 0) {
			temp.push(['Interest', parseInt(Stores.income.f1099_int.amt.value)])
		}
		if(Stores.income.gambling_other.amt.value > 0) {
			temp.push(['Gambling/Others', parseInt(Stores.income.gambling_other.amt.value)])
		}
		if(Stores.income.espp_discount.amt.value > 0) {
			temp.push(['ESPP Discount', parseInt(Stores.income.espp_discount.amt.value)])
		}
		if(Stores.income.f401k_match.amt.value > 0) {
			temp.push(['401k match', parseInt(Stores.income.f401k_match.amt.value)])
		}
		if(Stores.income.f1099_int_taxfree.amt.value > 0) {
			temp.push(['Tax-exempt interest', parseInt(Stores.income.f1099_int_taxfree.amt.value)])
		}
		setIncomeChartData(temp)

	}, [Stores.income.income.value]);

	useEffect(() => {
		let temp : any = []
		temp.push(['Label', 'Value'])

        let score = ((Stores.income.income.value - Stores.tax.due.value) / Stores.income.income.value) * 100
        temp.push(['Score', score])

        setGaugeData(temp)

    }, [Stores.tax.due.value])

	useEffect(() => {
    	let temp : any = []
		temp.push(['From', 'To', '$'])
		
		if(Stores.income.w2.value > 0) {
			temp.push(['W2', 'Adjusted Gross Income', parseInt(Stores.income.w2.value)])

            if(Stores.income.f401k_match.amt.value > 0) {
                temp.push(['W2', '401k + match', parseInt(Stores.income.f401k_match.amt.value) + parseInt(Stores.income.f401k_contrib.amt.value) ])
                temp.push(['401k + match', 'Pre-tax', parseInt(Stores.income.f401k_match.amt.value) + parseInt(Stores.income.f401k_contrib.amt.value) ])
                
            }
            if(Stores.income.hsa.amt.value > 0) {
                temp.push(['W2', 'HSA', parseInt(Stores.income.hsa.amt.value)])
                temp.push(['HSA', 'Pre-tax', parseInt(Stores.income.hsa.amt.value)])
            }
            if(Stores.income.fsa.amt.value > 0) {
                temp.push(['W2', 'FSA', parseInt(Stores.income.fsa.amt.value)])
                temp.push(['FSA', 'Pre-tax', parseInt(Stores.income.fsa.amt.value)])
            }
            temp.push(['W2', 'SSA + Medicare contrib.', Stores.income.ssa_medicare_contrib.value])
            temp.push(['SSA + Medicare contrib.', 'Social Security/Medicare', Stores.income.ssa_medicare_contrib.value])
		}
        if(Stores.income.pretax.value > 0) {
            temp.push(['Pre-tax', 'Tax-advantage Income', parseInt(Stores.income.pretax.value)])
        }

		if(Stores.income.f1099misc.amt.value > 0) {
			temp.push(['Misc/Nec/Rental', 'Adjusted Gross Income', parseInt(Stores.income.f1099misc.amt.value)])
		}
		if(Stores.income.rsu.amt.value > 0) {
			temp.push(['RSU/Options', 'Adjusted Gross Income', parseInt(Stores.income.rsu.amt.value)])
		}
		if(Stores.income.f1099b_longterm.amt.value > 0) {
			temp.push(['long-term cap gain', 'Adjusted Gross Income' , parseInt(Stores.income.f1099b_longterm.amt.value) ])
			temp.push(['Adjusted Gross Income', 'Tax-advantage cap gain', parseInt(Stores.income.f1099_div_qualified.amt.value)])
			temp.push(['Tax-advantage cap gain', 'Tax-advantage Income', parseInt(Stores.income.f1099_div_qualified.amt.value)])

		}
        if(Stores.income.f1099b_shortterm.amt.value > 0) {
			temp.push(['short-term cap gain', 'Adjusted Gross Income' , parseInt(Stores.income.f1099b_shortterm.amt.value)])
		}
		if(Stores.income.f1099r.amt.value > 0) {
			temp.push(['Retirement dist.', 'Adjusted Gross Income', parseInt(Stores.income.f1099r.amt.value)])
		}
		if(Stores.income.f1099g.amt.value > 0) {
			temp.push(['Unemployment benefits', 'Adjusted Gross Income', parseInt(Stores.income.f1099g.amt.value)])
		}
		if(Stores.income.farm.amt.value > 0) {
			temp.push(['Farm income', 'Adjusted Gross Income', parseInt(Stores.income.farm.amt.value)])
		}
		if(Stores.income.f1099_div.amt.value > 0) {
			temp.push(['Ordinary Dividend', 'Adjusted Gross Income', parseInt(Stores.income.f1099_div.amt.value)])
		}
		if(Stores.income.f1099_div_qualified.amt.value > 0) {
			temp.push(['Qualified Dividend', 'Adjusted Gross Income', parseInt(Stores.income.f1099_div_qualified.amt.value)])
			temp.push(['Adjusted Gross Income', 'Tax-advantage Dividend', parseInt(Stores.income.f1099_div_qualified.amt.value)])
			temp.push(['Tax-advantage Dividend', 'Tax-advantage Income', parseInt(Stores.income.f1099_div_qualified.amt.value)])
		}

        if(Stores.income.f1099_int.amt.value > 0) {
			temp.push(['Interest', 'Adjusted Gross Income', parseInt(Stores.income.f1099_int.amt.value)])
		}
		if(Stores.income.gambling_other.amt.value > 0) {
			temp.push(['Gambling/Others', 'Adjusted Gross Income', parseInt(Stores.income.gambling_other.amt.value)])
		}
		if(Stores.income.espp_discount.amt.value > 0) {
			temp.push(['ESPP Discount', 'Adjusted Gross Income', parseInt(Stores.income.espp_discount.amt.value)])
		}

		if(Stores.income.f1099_int_taxfree.amt.value > 0) {
			temp.push(['Tax-exempt interest', 'Pre-tax', parseInt(Stores.income.f1099_int_taxfree.amt.value)])
		}
        if(Stores.income.utma_gain.amt.value > 0) {
            temp.push(['UTMA gain', 'Pre-tax', parseInt(Stores.income.utma_gain.amt.value)])
		}
        if(Stores.income.ira.amt.value > 0) {
            temp.push(['Adjusted Gross Income', 'IRA contribution', parseInt(Stores.income.ira.amt.value)])
            temp.push(['IRA contribution', 'Pre-tax', parseInt(Stores.income.ira.amt.value)])
		}
        if(Stores.income.agi.value > 0) {
            temp.push(['Adjusted Gross Income', 'Deductions', parseInt(Stores.deduction.deduction.value[1])])
            temp.push(['Adjusted Gross Income', 'Taxable Income', parseInt(Stores.tax.taxable_income.value)])
            temp.push(['Deductions', 'Tax-advantage Income', parseInt(Stores.deduction.deduction.value[1])])
		}
        if(Stores.deduction.f529_contribution.amt.value > 0) {
            temp.push(['Taxable Income', '529 contribution', parseInt(Stores.deduction.f529_contribution.amt.value)])
            temp.push(['529 contribution', 'Tax-advantage Income', parseInt(Stores.deduction.f529_contribution.amt.value)])
		}
        if(Stores.deduction.roth_ira.amt.value > 0) {
            temp.push(['Taxable Income', 'Roth IRA contribution', parseInt(Stores.deduction.roth_ira.amt.value)])
            temp.push(['Roth IRA contribution', 'Tax-advantage Income', parseInt(Stores.deduction.roth_ira.amt.value)])
		}
        if(Stores.tax.taxable_income.value > 0) {
            if(Stores.tax.tax_amount.value > 0)
				temp.push(['Taxable Income', 'Tax Amount', Stores.tax.tax_amount.value])
			if(Stores.tax.taxable_income.value - Stores.tax.tax_amount.value > 0)
            	temp.push(['Taxable Income', 'Net Income', Stores.tax.taxable_income.value - Stores.tax.tax_amount.value - Stores.income.ssa_medicare_contrib.value])
        }
        if(Stores.tax.tax_amount.value > 0) {
			if(Stores.taxcredit.total_tax_credit.value > 0)
            	temp.push(['Tax Amount', 'Tax Credit', Stores.taxcredit.total_tax_credit.value])
			if(Stores.tax.due.value > 0)				
            	temp.push(['Tax Amount', 'Tax Due', Stores.tax.due.value])
			if(Stores.taxcredit.total_tax_credit.value > 0)				
            	temp.push(['Tax Credit', 'Tax-advantage Income', Stores.taxcredit.total_tax_credit.value])
        }

		setFlowData(temp)

	}, [Stores.deduction.deduction.value, Stores.income.agi.value, Stores.taxcredit.total_tax_credit.value]);

    return (
		<div style="padding:10px;">
			<p style="height:50px;"/>

			{showSuccessAlert()}

			<div class="container">
                <h5><i>income flow</i></h5>
                <div class="row align-items-start">
					<div class="col">
                    <Chart
                        chartType="Sankey"
                        width="1000px"
                        height="500px"
                        data={flowData}
                        options={options}
                        />

                    </div>
                </div>
                <hr/>
				<button type="button" class="btn btn-lg btn-success" onClick={() => {Stores.Serialize(); setShowAlertData(true)}}>Save Model locally</button>
				&nbsp;&nbsp;
				<button type="button" class="btn btn-lg btn-success" onClick={() => {Stores.ResetModel(); route('/basic');}}>Reset Model</button>
				<hr/>
				<h5><i>income analysis</i></h5>

				<div class="row align-items-start">
					<div class="col">
                        <div class="row align-items-start">
                            <Total store={Stores.income.income} title="Estimated Gross Income" 
	    						description="Total gross income from all income sources."/>
                        </div> 
                        <div class="row align-items-start">
                            <Total store={Stores.tax.taxable_income} title="Estimated Taxable Income" 
	    						description="Taxable income is AGI minus all deductions."/>
                        </div>
                        <div class="row align-items-start">
                            <div class="mb-3 border rounded" style="width:450px;padding:5px;">
                                <div class="input-group mb-3">
                                    <span class="input-group-text" style="width:180px;font-size:10pt;">Effective Tax Rate</span>
                                    <input  type="number" id="txt_total" value={Stores.tax.effective_tax_rate.value} aria-label="Total" class="form-control text-end" disabled={true}></input>
                                    <span class="input-group-text">%</span>
                                </div>
                                <label style="font-size:11px;padding-left:4px;">The real percentage of your taxable income that you pay in taxes is called your effective tax rate. This is due to marginal tax rate.</label>
                            </div>
                        </div>
                        <div class="row align-items-start">
                            <Total store={Stores.tax.tax_amount} title="Estimated Tax" 
	    						description="Amount of tax before tax credit. This is based on your filing status, income level and other considerations."/>
                        </div>  
                        <div class="row align-items-start">
                            <Total store={Stores.taxcredit.total_tax_credit} title="Estimated Tax Credit" 
    							description="Tax credits reduce the amount of income tax you owe to the federal. Credits are generally designed to encourage or reward certain types of behavior that are considered beneficial to the economy, the environment or to further any other purpose the government deems important."/>
                        </div>
                        
                        <div class="row align-items-start">
                            <Total store={Stores.tax.due} title="Estimated Due" 
    							description="Estimated Tax due. This is estimates for tax minus tax credit. Please note that negative means a refund, positive is tax owed. This is for modeling purpose only."/>
                        </div>

						<div class="row align-items-start">
								<h5><i>possible checklist</i></h5>
								<ul class="list-group" style="width:460px;">
									{ recommendationsData.map(reco => <li class="list-group-item" style="font-size:9.5pt;">{ reco }</li>) }
								</ul>
						</div>
						
                    </div>
					<div class="col">
                        <div class="row align-items-start" style="margin-left:50px;">

                            <Chart
                                width={250}
                                height={250}
                                chartType="Gauge"
                                data={gaugeData}
                                options={{
                                    greehFrom: 80,
                                    greenTo: 100,
                                    yellowFrom: 50,
                                    yellowTo: 80,
                                    redFrom: 0,
                                    redTo: 50,
                                    minorTicks: 5,
                                }}/>
                        </div> 

                        <div class="row align-items-start text-center">
                            <Chart
                                chartType="PieChart"
                                data={incomeChartData}
                                options={income_options}
                                width={"450px"}
                                height={"350px"}/>
                        </div>

                        <div class="row align-items-start text-center">
                            <Chart
                                chartType="PieChart"
                                data={deductionData}
                                options={deduction_options}
                                width={"450px"}
                                height={"350px"}/>

                        </div>     
                        <div class="row align-items-start text-center">
							<Chart
								chartType="PieChart"
								data={realDeductionData}
								options={real_deduction_options}
								width={"450px"}
								height={"350px"}/>
						</div>
                    </div>
				</div>

			</div>
		</div>
		
	);
};

export default Analysis;
